.quote-box {
  background: #252525;
  color: #e5e5e5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap-reverse;
      flex-wrap: wrap-reverse;
  margin: 20px 0;
  max-width: 1200px;
  padding: 25px;
}

.quote-box img {
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 100%;
}

@media (min-width: 800px) {
  .quote-box {
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    margin: 40px;
    padding: 40px 60px;
  }

  .quote-box img {
    -o-object-fit: contain;
       object-fit: contain;
    margin-right: 40px;
    width: auto;
  }
}



